import React, { Component } from 'react';

// Internal
import Header from './components/Header'
import LangSwitcher from './components/LangSwitcher';

import Intro from './components/Intro'
import Project from './components/Project'
import Technology from './components/Technology'

// api
import projects_en from './api/projects_en.json';
import projects_fr from './api/projects_fr.json';

import { getTechnologiesFromProjects } from './api/projectsUtils.js';

import './App.css';

const projects_map = {
  'en': projects_en,
  'fr': projects_fr,
}

class App extends Component {

  constructor (props) {
      
      super(props);

      this.state = {
        projects: [],
        technologiesFromProjects: [],
        activeLang: 'en',
        activeTechno: null
      }

  }

  onSwitchLang = (e) => {
    e.preventDefault();
    const lang = e.target.getAttribute('href');
    this.setState( (state ) => ( { 
      activeLang: lang,
      projects: projects_map[lang],
      technologiesFromProjects: getTechnologiesFromProjects(projects_map[lang])
    } ));
  }

  onFilterProjectsByTechno = (techno) => {
    
    if( techno === 'ALL') {
      this.setState( (state) => ( { 
        projects: projects_map[this.state.activeLang],
        activeTechno: null
      } ) );
      return;
    }
    
    const projects = projects_map[this.state.activeLang].filter( 
      (project) => project.technologies.filter(
          ( _techno ) => _techno === techno
        ).length
      );
    
    this.setState( (state) => ({ 
      projects,
      activeTechno: techno
    }) );

    const scroll_offset = document.getElementsByClassName('content')[0].offsetHeight + 20;
    
    window.scrollTo( 0, scroll_offset);

  }


  componentDidMount() {
    this.setState( (state) => ( { 
      projects: projects_map[this.state.activeLang],
      technologiesFromProjects: getTechnologiesFromProjects(projects_map[this.state.activeLang])
    } ) );
  }

  renderTechnologies = () =>  {

    // build an array of technologies objects
    // { name: 'techName', count: howManyProjectsUseIt }      
    return this.state.technologiesFromProjects.map( (techno, index) => {
        const onFilterProjects = this.onFilterProjectsByTechno.bind(null, techno.name);
        const className = this.state.activeTechno === techno.name ? 'active' : '';
        const count = parseInt(techno.count, 10)
        const weight_projects_max = 10;
        const weight =  count < weight_projects_max ? count / weight_projects_max * 2 : 1;
        const style= { backgroundColor: `rgba(0,200,200,${ weight } )`, color: 'black' }
        return (
          <li className={className} key={`techno_${index}`} onClick={onFilterProjects}>
            <Technology name={techno.name} projects_count={techno.count} style={style} />
          </li>
        );
      }
    );

  }

  renderProjects = () => {

    return this.state.projects.map( (project, index) => (
        <div key={`project_${index}`} className="project-container">
          <Project name={project.name} data={project} />
        </div>
      )
    );

  }

  renderIntro () {
    switch(this.state.activeLang) {
      case 'fr':
        return "['WordPress', 'WooCommerce', 'Javascript', 'React'] ...  de préférence";
      default :
        return <em>Memory Leak: Keeping a reference to an object ... Preventing it from being garbage collected ... </em>;
    }
  }

  renderBio () {
     switch(this.state.activeLang) {
      case 'fr':
        return <p>Je suis un développeur web autodidacte, front-end et back-end</p>
      default :
        return <div>
          <p>I am a self-taught web developer, front-end and back-end. I have been developing for the web for 15 years now and have participated in many projects ranging from simple websites to complex web applications. Although I can tackle different type of projects I now specialize in <a href="http://www.wordpress.org">WordPress</a>, <a href="http://www.woocommerce.com">Woocommerce</a> and modern javascript front-end built with <a href="http://www.wordpress.org">React.js</a>.</p>
          <p>I use development tools like <a href="https://git-scm.com/">Git</a>, <a href="http://www.gulpjs.com">Gulp</a>, <a href="https://webpack.github.io/">Webpack</a>, <a href="https://getcomposer.org/">Composer</a> and <a href="https://www.docker.com/">Docker</a> on a daily basis and like to use modern WordPress workflow like <a href="https://roots.io/bedrock/">Bedrock</a>.</p>
          <p>I am french and live In Paris but I am <em>quite comfortable with english</em>, in the work place and outside.</p>
          <p>Please find below the most noteworthy projects I have worked on and the technologies I have used.</p>
          <p>And of course don't hesitate get in touch for more details ... <a href="mailto:thomas.mery@gmail.com"><i className="fa fa-envelope" aria-hidden="true"></i><span style={{ display: 'none' }}>Email</span></a></p>
        </div>;
    }
  }


  render() {
    return (
      <div className="App container">

        <Header title="Thomas Mery | Web Development">
            <div className="row justify-content-between">
            <div className="col">
              <ul className="menu">
                <li><a href="mailto:thomas.mery@gmail.com"><i className="fa fa-envelope" aria-hidden="true"></i><span style={{ display: 'none' }}>Email</span></a></li>
                <li><a href="https://github.com/thomasmery" target="_blank" rel="noopener noreferrer"><i className="fa fa-github" aria-hidden="true"></i><span style={{ display: 'none' }}>Github</span></a></li>
              </ul>
            </div>
            <div className="col" style={{ textAlign: 'right' }}>
              <LangSwitcher onSwitchLang={this.onSwitchLang} activeLang={this.state.activeLang}/>
            </div>
          </div>
        </Header>

        <div className="row content">
          <div className="col">
              <Intro>
                {this.renderIntro()}
              </Intro>
              {this.renderBio()}
          </div>
        </div>
        <div className="row technologies-menu">
          <div className="col">
            <h3><button className="btn btn-outline-info btn-sm" style={{ opacity: this.state.activeTechno ? 1 : 0.4 }}onClick={ () => this.onFilterProjectsByTechno('ALL') }>View all Projects</button> <span style={{ fontSize: '0.6em' }}>... or filter by the technologies you're interested in.</span></h3>
            <ul className="list technologies__list">{ this.renderTechnologies() }</ul>
          </div>
        </div>
        <div className="row projects">
          <div className="col">
            <ul className="list projects__list">{ this.renderProjects() }</ul>
          </div>
        </div>
      
      </div>
    );
  }
}

export default App;



